html,
body {
  margin: 0;
  padding: 0;
}
body {
  padding: 20px;
}
canvas {
  display: block;
}
.inputs:not(:last-child) {
  padding-bottom: 5px;
}

#zoom-level,
#rotation,
#position {
  position: absolute;
  color: white;
  font-size: 0.8em;
  left: 25px;
}
#zoom-level {
  top: 22px;
}
#position {
  top: 36px;
}
#rotation {
  top: 50px;
}
